.Home {
  width: 100%;
  min-height: calc(100vh - 40px);
}
.HomeMain {
  width: 100%;
  min-height: calc(100vh - 130px);
  padding: 10px 40px;
  background-color: #e7eff6;
  border-radius: 4px;
}

@media (max-width: 1100px) {
  .Home {
    height: 100vh;
  }
  .HomeMain {
    min-height: calc(100vh - 70px);
  }
}
